var event = {
	/**
	 * @param {*} name 
	 * @param {*} fn 
	 */
	on : function(name , fn){
		!this.map[name] && (this.map[name] = []);
		this.map[name].push(fn);
	} ,
	/**
	 * @param {*} name 
	 * @param {*} fn name
	 */
	off : function(name , fn){
		if(!fn){
			return delete this.map[name];
		}
		var list = this.map[name];
		for(var i=0; i<list.length; i++){
			if(list[i] === fn){
				list.splice(i ,1);
				break;
			}
		}
		!list.length && delete this.map[name];
	},
	/**
	 * @param {*} name {name}
	 * @param {*} entry {entry}
	 * @param {*} once once=true{name}{name}
	 */
	emit : function(name , entry , once){
		var list = this.map[name];
		list && list.map((fn) => {
			fn(entry);
		});
		if(once){
			delete this.map[name];
		}
	},
	map : {
	}
}

if(!window.__event__){
	window.__event__ = event;
}
export default window.__event__;
