const { Title, Paragraph } = Typography;

function SystemAuthorization({userId}){
    let [visible ,setVisible] = useState(false);
    let [list , setList] = useState([]);
    useEffect(() => {
        lib.request({
            url : '/ucenter-customer/user/sub/system/openList' ,
            data : {userId},
            success : list => setList(list)
        })
    } , [])
    return (
        <>
            {visible && 
            <Modal visible={true} title='' footer={<Button type='primary' onClick={() => setVisible(false)}></Button>} >
                {
                    list.map(item => 
                        <div className='account-manage-system-auth' key={item.systemCode}>
                            <Switch checked={item.status == 1} onChange={(checked) => {
                                lib.request({
                                    url : `/ucenter-customer/user/sub/${checked ? 'open' : 'close'}System` ,
                                    data : {
                                        systemCode : item.systemCode,
                                        userId 
                                    },
                                    needMask : true,
                                    success : () => message.success('')
                                });
                                item.status = checked + 0;
                                setList([...list])
                            }}></Switch>{item.systemName}
                        </div>
                    )
                }
            </Modal>}
            <div className='link' onClick={() => setVisible(true)}></div>
        </>
    )
}

function BindRole({userId , userName }){
    let [visible ,setVisible] = useState(false);
    let [list , setList] = useState([]);
    let [roleId , setRoleId] = useState([]);
    let url = lib.config.webToken == 'admin' ? '/ucenter-admin/waiter/role/list' : '/ucenter-customer/user/sub/role/list';
    useEffect(() => {
        if(visible && list.length == 0){
            lib.request({
                url  ,
                data : {
                    userId ,
                    systemCode : lib.config.systemCode
                },
                success : list => {
                    setRoleId(list.filter(item => item.checked).map(item => item.id));
                    setList(list);
                    
                }
            })
        }
    } , [visible])
    return (
        <>
            {visible && 
            <Modal visible={true} title={``} width={600} className='account-manage-bind-role' maskClosable={false}
            onCancel={() => setVisible(false)} onOk={() => {
                let url = lib.config.webToken == 'admin' ? '/ucenter-admin/waiter/role/update' : '/ucenter-customer/user/sub/updateRole'
                lib.request({
                    url  , 
                    data : {
                        userId , systemCode : lib.config.systemCode ,roleId 
                    },
                    needMask: true , 
                    success: () => {

                        message.success('')
                        setVisible(false);
                    },
                })
            }}>
                <div className='item'>
                    <label></label>
                    {userName}
                </div>
                <div className='item'>
                    <label>():</label>
                    <Select
                        mode="multiple"
                        allowClear
                        style={{ width: '300px' }}
                        value={roleId}
                        onChange={value => setRoleId(value)}
                        >
                        {
                            list.map(item => <Select.Option key={item.id} value={item.id}>{item.name}</Select.Option>)
                        }
                    </Select>
                </div>
            </Modal>}
            <div className='link' onClick={() => setVisible(true)}></div>
        </>
    )
}

function InviteUser(){
    let [visible ,setVisible] = useState(false);
    let [email , setEmail] = useState('');
    return (
        <>
            {visible && 
                <Modal visible={true} title='' maskClosable={false}
                onCancel={() => setVisible(false)} 
                onOk={() => {
                    lib.request({
                        url : '/ucenter-admin/waiter/invite' , 
                        data : {
                            systemCode : lib.config.systemCode ,
                            email 
                        } ,
                        needMask : true , 
                        success : () => {
                            message.success('');
                            setEmail('');
                            setVisible(false);
                        }
                    })

                }}
                >
                    <Input placeholder='' value={email} onChange={e => setEmail(e.target.value)} />
                </Modal>
            }
            <Button  onClick={() => setVisible(true)}></Button>
        </>
    )
}

function CreateUser({onCreate}){
    let [visible ,setVisible] = useState(false);
    const [form] = Form.useForm();
    return (
        <>
            {visible && 
                <Modal visible={true} title='' onCancel={() => setVisible(false)} onOk={() => {
                    form.validateFields().then((values) => {
                        // form.resetFields();
                        lib.request({
                            url : '/ucenter-admin/waiter/create' ,
                            data : {
                                ...values , systemCode : lib.config.systemCode
                            },
                            needMask : true , 
                            success : () => {
                                form.resetFields();
                                message.success('');
                                setVisible(false);
                                onCreate();
                            }
                        })

                    })
                }}>
                    <Form form={form} layout="horizontal" name="form_in_modal" 
                        labelCol={{span: 5}} wrapperCol={{span: 16}}
                    >
                        <Form.Item name='userName' label='' 
                            validateTrigger={['onBlur' , 'onChange']}
                            rules={[
                                {required: true, message: '', validateTrigger:'onChange'},
                                {pattern: /[\w\W]{3,20}/ , message: '3-20' , validateTrigger:'onBlur'}
                            ]}
                        >
                            <Input  />
                        </Form.Item>
                        <Form.Item name='nickName' label='' rules={[
                        ]}>
                            <Input />
                        </Form.Item>
                        <Form.Item name='email' label='' 
                            rules={[
                                {required: true,  message: ''},
                                {
                                    type:'email' , message : 'xxx@163.com' 
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item name='mobile' label='' 
                            validateTrigger={['onBlur' , 'onChange']}
                            rules={[
                                {
                                    required: true,  message: '',
                                },
                                {
                                    pattern: /^\d{0,11}$/ , message: '111' , validateTrigger:'onChange'
                                },
                                {
                                    pattern: /^1\d{10}$/ , message: '111' , validateTrigger:'onBlur'
                                }
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item name='password' label='' 
                            rules={[
                                {required: true,  message: ''},
                                {validator: (rule, value) => {
                                    return new Promise((resolve ,reject) => {
                                        if(!/^([\x21-\x2f\x3a-\x40\x5b-\x60\x7B-\x7F]|\w){6,20}$/.test(value) || 
                                            /[\x21-\x2f\x3a-\x40\x5b-\x60\x7B-\x7F]+/.test(value) 
                                            + /\d+/.test(value) 
                                            + /[a-z]+/.test(value)
                                            + /[A-Z]+/.test(value)
                                            < 3
                                        ){
                                            reject('6-20');
                                        }else{
                                            resolve();
                                        }
                                    })
                              } }
                            ]}
                        >
                            <Input.Password />
                        </Form.Item>
                    </Form>
                </Modal>
            }
            <Button type='primary' onClick={() => setVisible(true)}></Button>
        </>
    )
}

