class DownloadCenter extends ConfigCenter {
    getOperation(row) {
        return (
            <>
                {
                    row.buttons.map((item, key) => {
                        return (
                            <span onClick={() => {
                                if(item == ''){
                                    window.open(row.url);
                                }
                                else {
                                    let map = {
                                        '' : '/park-gate/export/delete' ,
                                        '' : '/park-gate/export/cancel'
                                    }
                                    Modal.confirm({
                                        title : '' + item ,
                                        content : <>
                                            <div>: {row.name}</div>
                                            <div>: {row.finishTime}</div>
                                        </>,
                                        onOk :() =>{
                                            lib.request({
                                                url : map[item],
                                                data : {
                                                    uid : row.uid
                                                },
                                                needMask : true , 
                                                success : () => this.load()
                                            })
                                        }
                                    })
                                }
                            }}
                                style={{ marginRight: '10px' }} 
                                className='link' 
                                key={key}>
                                {item}
                            </span>
                        )
                    })
                }
            </>
        )
    }
    renderLeftOperation(){
        return <>
                <Alert style={{width : '450px' , display : 'inline-block' }} message='7' type="warning" showIcon  />
                <Button style={{marginLeft: '20px'}} type='primary' onClick={() => this.load(true)}></Button>
            </>
    }
}




export default function (props) {
    return <DownloadCenter {...props} config={config}></DownloadCenter>
};