import React from 'react';
import { ConfigCenter, lib } from '../../src/index';
import Util from './util';
import './index.less';
/*
//
 WAIT_AUTH(13, "", false),
 //
 AUTH_SUCCESS(16, "", false),
 //
 AUTH_FAIL(19, "", false);
 */
class OrderList extends ConfigCenter{
    constructor(props){
        super(props);
    }
    componentDidMount(){
    }
    
    renderTable(){
        var orderList = this.state.dataList?this.state.dataList:[];
        var timestamp = new Date().getTime();
        return (
            <table className='order-table'>
                <thead>
                    <tr>
                        <th colSpan="2" style={{minWidth : '462px'}}></th>
                        <th style={{minWidth : '130px'}}></th>
                        <th style={{minWidth : '130px'}}></th>
                        <th style={{minWidth : '152px', textAlign: 'center'}}></th>
                        <th style={{minWidth : '110px', textAlign: 'center'}}></th>
                    </tr>
                    
                </thead>
                <tbody>
                    {
                        orderList.map((order, index) => {
                            var itemList = order.skuOrders;
                            if(!itemList || !itemList.length){
                                itemList = [{}];
                            }
                            var num = (itemList).length ;
                            return (
                                <React.Fragment key={index}>
                                    <tr className='empty' ></tr>
                                    <tr className='summary-tr'>
                                        <td colSpan="6">
                                            <span>{order.orderSn}</span>
                                            <span>{order.outId}</span>
                                            <span>{order.shopName}</span>
                                            <span>{order.buyerName}</span>
                                            <span>{order.createdAt}</span>
                                        </td>
                                    </tr>
                                    {
                                        itemList.map((sku, skuIndex) =>
                                            <tr className="info-tr" key={skuIndex}>
                                                <td style={{paddingRight: 0, width: '89px'}}>
                                                    <img src={sku.skuImage}></img>
                                                </td>
                                                <td><label className={order.isBonded==1?'business-type bonded':'business-type normal'}>{sku.isBondedStr}</label>{sku.itemName}</td>
                                                <td>{Util.formatMoney(sku.fee)}</td>
                                                <td>{sku.quantity}</td>
                                                {skuIndex==0 &&
                                                    <React.Fragment>
                                                        <td className='bd-r bd-l fee-box' style={{textAlign: 'center'}} rowSpan={num}>
                                                            <span className='bold-text'>{Util.formatMoney(order.fee)}</span>
                                                            {order.isBonded==1 &&
                                                                <React.Fragment>
                                                                    <span className='light-word'>({Util.formatMoney(order.shipFee)}</span>
                                                                    <span className='light-word'>{Util.formatMoney(order.tax)})</span>
                                                                </React.Fragment>
                                                            }
                                                            {order.isBonded!=1 &&
                                                                    <span className='light-word'>({Util.formatMoney(order.shipFee)})</span>
                                                            }
                                                            
                                                        </td>
                                                        <td className='bd-r fee-box' style={{textAlign: 'center'}} rowSpan={num}>
                                                            <span className='status-lab'>{order.statusStr}
                                                            </span>
                                                            <div className='primary' onClick={() => lib.openPage(`/order-detail/${timestamp}?pageTitle=&orderId=${order.id}`)}>
                                                                
                                                            </div>
                                                        </td>
                                                    </React.Fragment>
                                                }
                                                
                                            </tr>
                                        )
                                    }
                                    
                                </React.Fragment>
                            )
                        })
                    }
                </tbody>
            </table>
        )
    }
}

export default OrderList
